候需要，事先準備好各人活動中所需之裝備（禦寒衣物、頭燈、雨具…等）及器具（碗、筷、乾糧…等）。"),_c('br'),_c('br'),_vm._v(" (7) MT10x10(山來水去企業社)活動團費皆包含200萬登山險或旅平險（不收團費之自費自理行程除外。）"),_c('br'),_c('br'),_vm._v(" (8) 請自行攜帶個人藥品，以避免藥物過敏、副作用等問題。"),_c('br'),_c('br'),_vm._v(" (9) 活動中，因個人因素（身體情況….等），無法繼續完成行程而撤退者，因之所產生的撤退費用需自理（人員支援、交通接駁….等），未完成行程，活動費用視同棄權無法退還。"),_c('br'),_c('br'),_vm._v(" (10) 活動期間，如遇人力不可抗拒之災害(颳風、冰雪、豪雨、坍方….等)而無法完成行程時，領隊可變更行程或食宿，如因之有節省支出費用，則於回程後平均歸還隊員。若因之活動需增加或更改食宿或改乘其他交通工具…等，所超支之費用，由參加者平均支付。"),_c('br'),_c('br'),_vm._v(" (11) 活動出發當日（包括之前），如遇各入山管理單位（警察局、國家公園…等）通知勸告不宜入山，或因人力不可抗拒之天然災害(颳風、冰雪、豪雨、坍方…等)而取消活動，扣除已支出支部分金額剩餘全額退費。"),_c('br'),_c('br'),_vm._v(" (12) 出發後如遇各入山管理單位（警察局、國家公園…等）通知勸告不宜入山，或因人力不可抗拒之天然災害(颳風、冰雪、豪雨、坍方…等)而取消活動則行程取消並退費，如已經出團，需扣除相關衍生的車資，或其他費用，其餘部分退還，再退回活動餘款。"),_c('br'),_c('br'),_vm._v(" (13) 活動需辦理入山証者，請於報名表中確實填寫真實資料，以便辦理。活動當日並需攜帶「身分證正本」備查，若無攜帶無法入山需自負責任。"),_c('br'),_c('br'),_vm._v(" (14) MT10x10（山來水去企業社）各活動隊均會拍攝活動中照片、影片，可能會刊登社群媒體或網站上。參加隊員必須同意活動中的肖像，MT10x10 （山來水去企業社）擁有免費使用權。事後自願提供自拍活動照片給MT10x10刊登或欣賞者，也將視為同意MT10x10 （山來水去企業社）擁有免費使用權。"),_c('br'),_c('br'),_vm._v(" (15) 活動人數未達最低開團人數，MT10x10 （山來水去企業社）得延期或退費，中籤後，若活動人數不足，可以補足最低開團人數全額費用，活動如期出發。"),_c('br'),_c('br'),_vm._v(" (16) 活動人員中籤後，人數未達預定基本開車人數而無法成團，不得要求索取「入園証」自行前往，入園証因是使用MT10x10 （山來水去企業社）相關名稱及IP申請， MT10x10需承擔活動中任何事項，因此不是MT10x10 （山來水去企業社）所承辦的活動，是無法使用MT10x10 （山來水去企業社）所申請之入園証，敬請見諒。"),_c('br'),_c('br'),_vm._v(" (17) 戶外氣候無常，需有心理準備，如遇天候風雨過大或持續，帳蓬會有吹走或進水、背包可能會淋濕…等，可能狀況發生，會將衣物、睡袋弄溼。請事先以塑膠袋，分袋打包好易溼衣物、食物、重要證件…等。"),_c('br'),_c('br'),_vm._v(" (18) 報名活動如有不搭車、不住宿、不慶功、或自行開車登山口報到、住宿方式改變等等特殊需求，請於報名時就說明詢問指定，報名完成及抽籤後，請恕無法更動。"),_c('br'),_c('br'),_vm._v(" (19) 行程開始前4-14天我們會在line群組發布行前手冊、裝備租借統計表、行前會時間地點、路線詳細介紹文章。"),_c('br'),_c('br'),_vm._v(" (20) 行程中，若有夥伴發生足以危害生命安全之狀況，則全隊共同進退，不得異議，且因登山隊伍在法律上屬【危險共同體】，故全隊應共同進退！"),_c('br'),_c('br'),_vm._v(" 「不需抽籤之行程」退費機制："),_c('br'),_vm._v(" 活動成行後，因個人因素取消或延期。"),_c('br'),_vm._v(" 1.出發前第31日以前取消者，收取行政費用200元"),_c('br'),_vm._v(" 2.出發前第15日至30日以內取消者，退總費用60%"),_c('br'),_vm._v(" 3.出發前第7日至14日以內取消者，退總費用40%"),_c('br'),_vm._v(" 4.出發前第3日至6日以內取消者，退總費用20%"),_c('br'),_vm._v(" 5.出發前3日內，恕無法退還任何費用"),_c('br'),_vm._v(" 「需抽籤之行程」退費機制："),_c('br'),_vm._v(" 未成行或由我方取消出團"),_c('br'),_vm._v(" 可選擇全額保留到其他行程或退費"),_c('br'),_vm._v(" 人數不足以出團則全額退費"),_c('br'),_vm._v(" 未中籤自行取消退款須扣除＄200手續費"),_c('br'),_vm._v(" 管理處規定申請入山入園且中籤後『無法更換人員』"),_c('br'),_vm._v(" 為符合公平性原則並且保障全團出團權益"),_c('br'),_vm._v(" 抽中山屋後如人員因故不能成行"),_c('br'),_vm._v(" 將酌收行程總費用的50%"),_c('br'),_vm._v(" 當作取消費，以補足共攤費用"),_c('br'),_vm._v(" 出發前第31日以前取消者，退總費用50%"),_c('br'),_vm._v(" 出發前第15日至30日以內取消者，退總費用40%"),_c('br'),_vm._v(" 出發前第7日至14日以內取消者，退總費用30%"),_c('br'),_vm._v(" 出發前第3日至6日以內取消者，退總費用20%"),_c('br'),_vm._v(" 出發前3日內，恕無法退還任何費用"),_c('br')])])}]

